import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';

import { Inbound } from '../../model/Inbound';

@IonicPage()
@Component({
  selector: 'page-inbound-detail',
  templateUrl: 'inbound-detail.html'
})
export class InboundDetailPage {
  inbound: any;

  constructor(public navCtrl: NavController, navParams: NavParams, feed: Array<Inbound>) {
    this.inbound = navParams.get('inbound') || feed.length > 0 ? feed[0] : null;
  }

}



